import React, { useEffect, useState } from "react";
import {
  CCard,
  CCol,
  CRow,
  CCardHeader,
  CCardBody,
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CFormText,
  CSelect,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import { useDispatch, useSelector } from "react-redux";

import {
  GetDataJabatanAction,
  setFormJabatanAction,
  PostJabatanAction,
  setJabatanIdAction,
  UpdateJabatanAction,
  DeleteJabatanAction,
} from "src/common/redux/action";

const Jabatan = () => {
  const [stateUpdate, setStateUpdate] = useState(false);
  const { GetDataJabatan, SetFormJabatan, modalJabatan } = useSelector(
    (state) => state.masterJabatanReducer
  );
  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <button
              type="button"
              className="btn btn-sm btn-dark text-center mr-2"
              onClick={() => handleModalJabatan("edit", rowData.idJabatan)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() => prosesDelete(rowData.idJabatan, rowData.nama)}
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Kode", field: "kode" },
    { title: "Nama ", field: "nama" },
  ];

  const {
    IdJabatan,
    Kode,
    Nama
  } = SetFormJabatan;
  const dispatch = useDispatch()

  const SetDataJabatanAPI = () =>
    dispatch(GetDataJabatanAction("GET_LIST_JABATAN"));

  const showModal = () =>
    dispatch({ type: "MODAL_JABATAN", payload: !modalJabatan });

  const handleModalJabatan = (type, idJabatan) =>{
      if (type == "add_jabatan") {
        showModal();
        setStateUpdate(false)
        dispatch({ type: "RESET_FORM_JABATAN" });
      }else{
        showModal();
        setStateUpdate(true);
        dispatch(setJabatanIdAction(idJabatan));
      }
  }
  const prosesDelete = (id, nama) => dispatch(DeleteJabatanAction(id, nama));

  const prosesJabatanAdd = () => dispatch(PostJabatanAction(SetFormJabatan));

  const prosesJabatanEdit = () => dispatch(UpdateJabatanAction(SetFormJabatan));

  useEffect(() => {
    SetDataJabatanAPI()
  }, [dispatch]);
  //console.log(GetDataJabatan);
  return (
    <CRow>
      <CCol md="12" xl="12" xs="12">
        <CCard>
          <CCardHeader>
            <CCol md="3">
              <CButton
                block
                color="primary"
                onClick={() => handleModalJabatan("add_jabatan")}
              >
                <FontAwesomeIcon icon="plus" />
                Tambah
              </CButton>
            </CCol>
          </CCardHeader>
          <CCardBody>
            <MaterialTable
              title=""
              options={{
                headerStyle: {
                  backgroundColor: "#01579b",
                  color: "#FFF",
                },
                filtering: true,
              }}
              columns={columns}
              data={GetDataJabatan.length > 0 ? GetDataJabatan : []}
            />
          </CCardBody>
        </CCard>
      </CCol>

      <CModal show={modalJabatan} onClose={showModal} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>Form Jabatan</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-kode"> Kode </CLabel>
              <CInput
                value={Kode}
                placeholder="input kode jabatan ..."
                onChange={(e) =>
                  dispatch(setFormJabatanAction("Kode", e.target.value))
                }
              ></CInput>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama">Nama</CLabel>
              <CInput
                type="text"
                id="nf-kode"
                name="nama"
                placeholder="input nama jabatan ..."
                value={Nama}
                onChange={(e) =>
                  dispatch(setFormJabatanAction("Nama", e.target.value))
                }
              />
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={stateUpdate ? prosesJabatanEdit : prosesJabatanAdd}
          >
            {stateUpdate ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModal}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </CRow>
  );
};

export default Jabatan;
